/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";
import { useState, useRef, useEffect, useCallback } from "react";
import { ReactComponent as Plus } from "../images/plus.svg";
import { ReactComponent as Minus } from "../images/minus.svg";
import { ReactComponent as Play } from "../images/play.svg";
import { ReactComponent as Pause } from "../images/pause.svg";
import { ReactComponent as Stop } from "../images/stop.svg";
import { ReactComponent as Lap } from "../images/lap.svg";

const Timer = () => {
  const [ms, setMs] = useState(0); //  ( 1/1000)
  const [second, setSecond] = useState(0); // 
  const [minute, setMinute] = useState(0); // 
  const [hour, setHour] = useState(0); // 
  const [lap, setLap] = useState([]); // 
  const [status, setStatus] = useState("stop");
  const speed = useRef(300); //      
  const timeout = useRef(null); //        setTimeout 
  const playTimeout = useRef(null); //     Interval   
  const startTime = useRef(null); //    ref
  const setTime = useRef(null); // play      ref 

  //    

  const onIncrease = () => {
    setStatus("increase");
    increase();
  };

  const increase = useCallback(() => {
    // hour, minute, second     useCallback 
    if (speed.current > 30) speed.current -= 10;
    if (second < 59) setSecond(second + 1);
    else {
      // 1000    
      if (hour === 999 && minute === 59 && second === 59) {
        setStatus("stop");
        clearTimeout(playTimeout.current);
      } else if (minute < 59) {
        setSecond(0);
        setMinute(minute + 1);
      } else {
        setSecond(0);
        setMinute(0);
        setHour(hour + 1);
      }
    }
  }, [hour, minute, second]);

  const onDecrease = () => {
    setStatus("decrease");
    decrease();
  };

  const decrease = useCallback(() => {
    // hour, minute, second     useCallback 
    if (speed.current > 30) speed.current -= 10;
    if (second > 0) {
      if (hour === 0 && minute === 0 && second === 1) {
        setStatus("stop");
        clearTimeout(playTimeout.current);
      }
      setSecond(second - 1);
    } else {
      if (minute > 0) {
        setSecond(59);
        setMinute(minute - 1);
      } else {
        setSecond(59);
        setMinute(59);
        setHour(hour - 1);
      }
    }
  }, [hour, minute, second]);

  const offPress = () => {
    setStatus("pause");
    speed.current = 300;
    clearTimeout(timeout.current);
  };

  const secondChange = (e) => {
    timePause();
    const originalValue = e.target.value;
    const onlyNumber = originalValue.replace(/[^0-9]/g, "");
    if (!onlyNumber) {
      setSecond(0);
    } else if (+onlyNumber > 59) {
      // 60  
      setSecond(0);
    } else {
      setSecond(+onlyNumber);
    }
  };

  const minuteChange = (e) => {
    timePause();
    const originalValue = e.target.value;
    const onlyNumber = originalValue.replace(/[^0-9]/g, "");
    if (!onlyNumber) {
      setMinute(0);
    } else if (+onlyNumber > 59) {
      // 60  
      setMinute(0);
    } else {
      setMinute(+onlyNumber);
    }
  };

  const hourChange = (e) => {
    timePause();
    const originalValue = e.target.value;
    const onlyNumber = originalValue.replace(/[^0-9]/g, "");
    if (!onlyNumber) {
      setHour(0);
    } else if (+onlyNumber > 999) {
      // 999  0 
      setHour(0);
    } else {
      setHour(+onlyNumber);
    }
  };

  //    

  const timePlay = () => {
    //  
    if (startTime.current === null && status !== "play") {
      startTime.current = Date.now();
      setTime.current = new Date(
        second * 1000 + minute * 1000 * 60 + hour * 1000 * 60 * 60
      );
    }

    if (!(hour === 0 && minute === 0 && second === 0)) setStatus("play"); //    play play   X
  };

  const timeLap = () => {
    if (status === "play")
      setLap((prev) => [...prev, { hour, minute, second }]);
  };

  const timePause = () => {
    setStatus("pause");
    if (status === "play") {
      clearInterval(playTimeout.current);
      startTime.current = null;
      setTime.current = null;
    }
  };

  const timeReset = () => {
    setStatus("stop");
    setLap([]);
    clearInterval(playTimeout.current);
    setMs(0);
    setSecond(0);
    setMinute(0);
    setHour(0);
    startTime.current = null;
    setTime.current = null;
  };

  useEffect(() => {
    const cal = new Date(
      setTime.current - (Date.now() - startTime.current) + 999
    ); //     1  0      999 
    // play   
    if (status === "play") {
      if (hour === 0 && minute === 0 && second === 0) {
        clearTimeout(playTimeout.current);
        setStatus("stop");
        startTime.current = null;
        setTime.current = null;
      } else {
        playTimeout.current = setTimeout(() => {
          setMs(cal.getUTCMilliseconds());
          setSecond(cal.getUTCSeconds());
          setMinute(cal.getUTCMinutes());
          setHour(cal.getUTCHours());
        }, 1);
      }
    }
    if (
      status === "pause" ||
      status === "stop" ||
      status === "increase" ||
      status === "decrease"
    ) {
      startTime.current = null;
      setTime.current = null;
    }

    return () => clearTimeout(playTimeout.current);
  }, [hour, minute, second, ms, status]);

  useEffect(() => {
    if (status === "increase") {
      timeout.current = setTimeout(() => {
        increase();
      }, speed.current);
    } else if (status === "decrease") {
      timeout.current = setTimeout(() => {
        decrease();
      }, speed.current);
    }
  }, [increase, decrease, status]);

  return (
    <div css={totalContainer({ status, hour, minute, second })}>
      <div css={watchContainer}>
        <Plus
          alt=""
          onMouseDown={onIncrease}
          onMouseUp={offPress}
          onMouseLeave={offPress}
          className="plus"
        />
        <input
          type="text"
          value={hour >= 10 ? hour : "0" + hour}
          onChange={hourChange}
        ></input>
        <span>&nbsp;:&nbsp;</span>
        <input
          type="text"
          value={minute >= 10 ? minute : "0" + minute}
          onChange={minuteChange}
        ></input>
        <span>&nbsp;:&nbsp;</span>
        <input
          type="text"
          value={second >= 10 ? second : "0" + second}
          onChange={secondChange}
        ></input>
        <Minus
          alt=""
          onMouseDown={onDecrease}
          onMouseUp={offPress}
          onMouseLeave={offPress}
          className="minus"
        />
      </div>
      <div css={playContainer({ status })}>
        <Play onClick={timePlay} className="play" />
        <Lap onClick={timeLap} className="lap" />
        <Pause onClick={timePause} className="pause" />
        <Stop alt="" onClick={timeReset} className="stop" />
      </div>
      <div css={lapContainer}>
        <h3>-- lap --</h3>
        {lap.map((elem, index) => (
          <p key={index}>
            {elem.hour >= 10 ? elem.hour : "0" + elem.hour} :{" "}
            {elem.minute >= 10 ? elem.minute : "0" + elem.minute} :{" "}
            {elem.second >= 10 ? elem.second : "0" + elem.second}
          </p>
        ))}
      </div>
    </div>
  );
};

const totalContainer = (props) => css`
  padding: 1.5rem;
  background-color: ${props.status === "play" &&
  props.hour === 0 &&
  props.minute === 0 &&
  props.second === 3
    ? "#ffd3d3"
    : props.status === "play" &&
      props.hour === 0 &&
      props.minute === 0 &&
      props.second === 2
    ? "#ff9b9b"
    : props.status === "play" &&
      props.hour === 0 &&
      props.minute === 0 &&
      props.second === 1
    ? "#ff5e5e"
    : "white"};
`;

const watchContainer = css`
  display: flex;
  justify-content: center;
  line-height: 50px;

  input {
    width: 70px;
    font-family: "HSYuji-Regular";
    font-weight: 600;
    font-size: 2rem;
    text-align: center;
  }

  span {
    font-weight: 600;
    font-size: 2rem;
  }

  .plus:hover,
  .minus:hover {
    transform: scale(1.04, 1.04);
    transition: all ease 0.1s;
  }

  .plus:hover {
    fill: #bcfcff;
  }

  .minus:hover {
    fill: #ffc4c4;
  }
`;

const playContainer = (props) => css`
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;

  & :not(:first-of-type) {
    margin-left: 1rem;
  }

  .play {
    fill: ${props.status === "play" ? "red" : "black"};
  }

  .play,
  .lap,
  .pause,
  .stop {
    cursor: pointer;
  }

  .play:hover,
  .lap:hover,
  .pause:hover,
  .stop:hover {
    transform: scale(1.04, 1.04);
    transition: all ease 0.1s;
    fill: red;
  }
`;

const lapContainer = css`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 0rem;
  font-family: "HSYuji-Regular";

  h3 {
    margin-top: 0px;
  }

  p {
    margin: 0px;
  }
`;

export default Timer;
